.form {
    text-align: center;
  }
  
  .todo-list {
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: normal;
    margin: 12px;
    padding-bottom: 12px;
  }
  
  .list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 10px 50px;
    padding: 7px;
    background-color: white;
    border-radius: 7px;
    cursor: pointer;
  }
  
  .form-input {
    font-size: 17px;
    margin: 5px;
    padding: 3px;
  }
  .edit-text {
    text-align: center;
    color: lightslategray;
    padding-bottom: 10px;
  }
  
  .btn {
    font-size: 17px;
    padding: 2px;
    border-radius: 7px;
    cursor: pointer;
    background-color: lightskyblue;
  }
  .btn:hover {
    background-color: #db7c26;
  }
  
  .delete-icon {
    color: red;
  }
  
  .edit {
    position: relative;
    right: 33px;
    color: green;
  }
  
  .text {
    overflow-wrap: anywhere;
  }
  